@import '../styles/variables';

.PrioritizationGridTitle {
    position: absolute;
    top: $prioritization-grid-top - 100px;
    left: $prioritization-grid-left;
    font-size: 22px;
    font-weight: bold;
}

// format a button element inside PrioritizationGrid class
.PrioritizationGridTitle button {
    position: absolute;
    top: $prioritization-grid-top - 120px;
    left: $prioritization-grid-left + 420px;
    width: 120px;
    height: 40px;
    font-size: 15px;
    font-weight: bold;
    border: 1px solid #000;
    border-radius: 5px;
    cursor: pointer;
}

// format an input element inside PrioritizationGrid class
.PrioritizationGridTitle input {
    display: none;
}

// format a label element inside PrioritizationGrid class
.PrioritizationGridTitle label {
    // change the background colour to red
    background-color: buttonface;
    text-align: center;
    line-height: 38px;
    position: absolute;
    top: $prioritization-grid-top - 120px;
    left: $prioritization-grid-left + 560px;
    width: 120px;
    height: 38px;
    font-size: 15px;
    font-weight: bold;
    border: 1px solid #000;
    border-radius: 5px;
    cursor: pointer;
}

// give the label element a hover effect
.PrioritizationGridTitle label:hover {
    background-color: #fffffff9;
}

// give the export button a hover effect
.PrioritizationGridTitle button:hover {
    background-color: #ffffff;
}

.PrioritiesTitle {
    z-index: -1;
    rotate: $priortization-lists-rotate;
    position: absolute;
    text-align: center;
    width: 320px;
    height: 90px;
    border: none;
    resize: none;
    border: 0px solid black;
    top: $prioritization-grid-top + 20px;
    left: $prioritization-grid-left - 60px;
    font-size: 39px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
}

.PrioritiesTitle:focus {
    border: none;
    outline: none !important;
}

.PrioritiesTitleLabel {
    z-index: -1;
    rotate: $priortization-lists-rotate;
    position: absolute;
    font-weight: bold;
    font-size: medium;
    top: $prioritization-grid-top;
    left: $prioritization-grid-left + 25px;
}